a:focus {
  outline: dotted thin;
}
a:active, a:hover {
  outline: 0;
}
a:active, a:link, a:visited {
  color: #fff;
  text-decoration: none;
}
a {
  font: 16px/24px Asap-Medium, Arial, Verdana, sans-serif;
  font-weight: 700;
  font-style: normal;
}
a:active {
  outline: 0;
}
a:not(.hover-none):hover {
  color: #ccc;
  text-decoration: none;
}

#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 100;
}

.header-inner {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  max-width: 1400px;
  margin: 0 auto;
  text-align: left;
}

#logo {
  position: absolute;
  top: 35px;
  left: 80px;
  width: auto;
  height: 30px;
}

#logo img, #logo svg {
  display: block;
  width: auto;
  height: 100%;
}

#logo .fill-white {
  fill: #fff;
}

#navigation {
  display: inline-block;
  left: 200px;
}

#navigation, #navigation-right {
  position: absolute;
  top: 35px;
  height: 30px;
}

.nav-links {
  position: relative;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.nav-links li {
  position: relative;
  display: inline-block;
  margin: 0 25px;
}
.nav-links li a {
  display: block;
  line-height: 1.4;
}
.nav-links a {
  position: relative;
  font: 17px/33px Sarabun-SemiBold, Arial, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  text-transform: uppercase;
  -webkit-transition: color .25s ease;
  transition: color .25s ease;
}

#wrapper, .background, .knockout, .section, .section-inner {
  display: block;
  width: 100%;
}

#wrapper {
  overflow-x: hidden;
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 1;
}

.btn .text, .section {
    position: relative;
}


#scroller {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  will-change: transform;
}

.background, .knockout {
  position: absolute;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #333;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}


.section {
  height: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  position: relative;
  overflow: auto;
  display: block;
}

.section--auto {
  height: auto;
  min-height: 100%;
}


.inner-left-bottom {
  position: absolute;
  bottom: 15%;
  left: 150px;
  text-align: left;
}
.inner-center-bottom {
  position: absolute;
  width: 80%;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  text-align: center;
}
.inner-right-bottom {
  position: absolute;
  bottom: 15%;
  right: 150px;
  text-align: left;
}
.inner-center-middle {
  position: absolute;
  width: 80%;
  bottom: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  text-align: center;
}

.indent {
  margin-right: -20px;
}

.inspiration {
  font: 20px/24px Sarabun-Italic, Arial, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.6;
  margin-bottom: 30px;
}


.inner-left-top {
  position: absolute;
  top: 15%;
  left: 100px;
  width: 50%;
  text-align: left;
}

table {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 35px;
    margin-right: -20px;
}
table td {
    font: 16px/18px Sarabun-Medium, Arial, Verdana, sans-serif;
    text-align: left;
    color: #fff;
    padding: 20px 0;
    border-bottom: 1pt solid rgba(255, 255, 255, .3);
}
table.data td {
    text-align: right;
}
table.data td:first-child {
    font: 16px/18px Sarabun-SemiBold, Arial, Verdana, sans-serif;
    text-align: left;
}
table.data td span {
    color: #868686;
}

table.data a {
  font-weight: 400;
}


h1, h2, h3 {
  position: relative;
  text-transform: uppercase;
  color: #fff;
}
h1 {
  font: 80px/70px Sarabun-SemiBold, Arial, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: auto;
}
h2 {
  font: 46px/50px Sarabun-SemiBold, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: auto;
}
h3 {
  font: 30px/34px Sarabun-SemiBold, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: auto;
}
h4 {
  position: relative;
  font: 20px/38px Sarabun-Regular, Arial, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  margin: auto;
}

p {
  position: relative;
  font: 18px/24px Sarabun-Regular, Arial, Verdana, sans-serif;
  font-weight: 400;
  font-style: normal;
  line-height: 1.6;
  color: #fff;
}

p.team {
  font: 18px/24px Sarabun-SemiBold, Arial, Verdana, sans-serif;
  padding-top:5px;
  margin-bottom:-15px;
}
